import React from 'react';
import { NavLink } from 'react-router-dom';
import Axios from 'axios';
import conf from '../../config';
import { addFilter, filterData } from '../../Actions/filter';
import { connect } from 'react-redux'
import { setCategory } from '../../Actions/content';

class FeatureNav extends React.Component{
  state = {
    categories: false
  }
  componentDidMount() {
    Axios.get(`${conf.server}/api/category`).then((res) => {
      this.setState({
        categories: res.data
      });
      this.props.setCategory(res.data)
    })
  }
  render(){
    // console.log(this.state.categories)
    return(
      <ul className="nav flex-column bg-white py-4 pl-2 feature-nav">
        {this.state.categories && this.state.categories.map((category) => {
          return (
            <li className="nav-item" key={category._id}>
              <NavLink 
                className="nav-link text-dark" 
                to={`#${category._id}`}
                onClick={(e) => {
                  // this.props.addFilter(category);
                  this.props.filter(category._id);
                }}
              >{category.name}
              </NavLink>
            </li>
          )
        })}
      </ul>
    )
  }
}
const mapDispatchToProps = (dispatch) => ({
  addFilter: (filter) => dispatch(filterData(filter)),
  setCategory: (data) => dispatch(setCategory(data)),  
})

export default connect(undefined, mapDispatchToProps)(FeatureNav);