import React from 'react';
import Axios from 'axios';
import { connect } from 'react-redux';
import Row from '../Row';
import Card from '../Card';
import conf from '../../config';
import Loader from '../Loader';
import { MyImage, deleteContent, DeleteImage } from '../../Actions/content';

class MyCategory extends React.Component{
  state = {
    data: false
  }
  componentDidMount(){
    Axios.get(`${conf.server}/api/category`).then((res) => {
      this.setState({
        data: res.data
      })
    }).catch((e) => {
      console.log(e);
      this.setState({
        data: undefined
      })
    })
  }


  render(){
    return (
      <div className="col-md-9">
        <ul>
        {this.state.data && this.state.data.map((cat) => {
          return <li key={cat._id}>
                    <button className="btn btn-pri my-1 w-50" 
                      onClick={() => {
                        Axios.delete(`${conf.server}/api/category/${cat._id}`, {
                          headers: {
                            'authorization': localStorage.getItem('auth')
                          }
                        }).then((res) => {
                          const newData = this.state.data.filter((cat) => {
                            return cat._id != res.data._id
                          })
                          this.setState({
                            data: newData
                          })
                        }).catch((e) => {
                          console.log(e)
                        })
                      }}
                    >{cat.name}</button>
                  </li>
        })}
        </ul>
        {!this.state.data && this.state.data != undefined && <Loader/>}
      </div>
    )
  }
}

const mapStateToProps = (state) => ({
  images: state.content.images
})
const mapDispatchToProps = (dispatch) => ({
  MyImage: (data) => (dispatch(MyImage(data))),
  DeleteImage: (id) => dispatch(DeleteImage(id))
})

export default (MyCategory);